function Charts({ address }) {

  const [user, setUser] = useState({})
  const [userEligible, setUserEligible] = useState(true)
  const [numberCrunch, setNumberCrunch] = useState([])

  useEffect(() => {
    if (address === '' || address === undefined) return
    setUserEligible(true)
    const user = losses.find(o => o.address === address.toLowerCase())
    if (user === undefined) {
      setUserEligible(false)
    }

    // for (let i = 0; i < user.events.length; i++) {
    //   let event = user.events[i]

    //   if (event.type === "buy") {
    //     setNumberCrunch(numberCrunch => [...numberCrunch, <p>{numberCrunch + (i+1).toString() + " You BOUGHT -" + event.amount.toString() + 'BNB worth of MOAI\n'}</p>]);
    //   }
    //   if (event.type === "sell") {
    //     setNumberCrunch(numberCrunch => [...numberCrunch, <p>{numberCrunch + (i+1).toString() + " You SOLD +" + event.amount.toString() + 'BNB worth of MOAI\n'}</p>]);
    //   }
    // }
    
    setUser(user)
  }, [address])

  return (
    <>
      <PageTitle>Loss Recovery Program - MAKE SURE YOU HAVE SOLD ALL YOUR MOAI PRIOR TO USING THIS</PageTitle>

      <Card colored className={`text-white mb-8 ${!userEligible || user.totalLoss === undefined ? 'bg-purple-600' : user.totalLoss < 0 ? 'bg-red-600' : 'bg-green-500'}`}>
          <CardBody>
            <p className="mb-1 font-semibold text-2xl text-center">{!userEligible || user.totalLoss === undefined ? 'Enter Your Address To Determine If You\'re Eligible For The Presale Whitelist' : user.totalLoss < 0 ? 'You are not currently eligible for the presale whitelist, but you can participate in the open presale. Keep an eye on the Telegram main chat for details.' : 'You are eligible for the presale whitelist, please keep an eye out on the Telegram main chat for details'}</p>
            <p className="text-xl">
            
            </p>
          </CardBody>
        </Card>

      <div className="grid gap-6 mb-8 md:grid-cols-2">
        <Card colored className="text-white bg-purple-600">
          <CardBody>
            <p className="mb-4 font-semibold text-2xl">Your Address</p>
            <p className="text-xl">
            {userEligible ? user.address === undefined ? "Please enter your address at the top of the page" : user.address : "Your address is ineligible for recovery claims. Please make sure you have submitted the Google Docs loss form posted in the telegram group. If you submitted recently, please wait for us to update the array."}
            </p>
          </CardBody>
        </Card>

        <Card colored className="text-white bg-purple-600">
          <CardBody>
            <p className="mb-4 font-semibold text-2xl">Dividends You Earned</p>
            <p className="text-xl">
            {!userEligible || user.dividends === undefined  ? '0' : user.dividends} <span className="font-bold">BNB</span>
            </p>
          </CardBody>
        </Card>
      </div>


      <Card colored className="text-white bg-purple-600 text-center">
          <CardBody>
            <p className="mb-4 font-semibold text-2xl">Total Loss To Be Returned Via TIKI Recovery Protocol</p>
            <p className="text-xl mb-4">
            {numberCrunch}
            {!userEligible || user.totalLoss === undefined ? '0' : user.totalLoss} <span className="font-bold">BNB</span>
            </p>
            <p className="text-xl mb-4 font-semibold">{!userEligible || user.totalLoss === undefined ? '' : user.totalLoss < 0 ? 'You are in PROFIT according to the transactions below. You will not be part of the recovery program.' : 'In addition, you will also be airdropped TIKI tokens proportional to your share of the total losses as a gesture of goodwill'}</p>
            <h4>Please note that the full recovery of your total loss is entirely dependent on the growth and success of TIKI. We cannot guarantee success in this regard, but the protocol is designed to reimburse your loss through activity on TIKI over time.</h4>
          </CardBody>
        </Card>

      <PageTitle>MOAI Transactions Used To Calculate Your Losses</PageTitle>
      <TableContainer className="mb-8">
        <Table>
          <TableHeader>
            <tr>
              <TableCell>Transaction</TableCell>
              <TableCell>BNB Transacted</TableCell>
              <TableCell>Transaction Type</TableCell>
              <TableCell>Block</TableCell>
            </tr>
          </TableHeader>
          <TableBody>
            {user !== undefined && userEligible && user.events !== undefined ? user.events.map((event, i) => (
              <TableRow key={i}>
                <TableCell>
                  <div className="flex items-center text-sm">
                    <div>
                      <a href={`https://bscscan.com/tx/${event.transactionHash}`}  target="_blank" rel="noopener noreferrer" className="font-semibold">{event.transactionHash}</a>
                    </div>
                  </div>
                </TableCell>
                <TableCell>
                  <span className="text-sm">{event.amount}</span>
                </TableCell>
                <TableCell>
                  <Badge type={event.type === "sell" ? 'danger' : 'success'}>{event.type}</Badge>
                </TableCell>
                <TableCell>
                  <span className="text-sm">{event.blockNumber}</span>
                </TableCell>
              </TableRow>
            )) : null}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  )
}

export default Charts
